import $ from '@zz-yy/ajax-lite';
import ZZUTIL from '../../common/util';

const queryString = require('query-string');

class Jd extends React.Component {
  constructor() {
    super();
    this.parsed = queryString.parse(window.location.search);
    this.callapp = this.callapp.bind(this);
  }

  componentWillMount() {
    this.clickLog({ actiontype: 'SEM40-VIEW1' });
  }

  componentDidMount() {
    setTimeout(
      () => {
        if (this.parsed.isauto === 'true') {
          this.callapp();
        }
      }, 1700
    );
  }

  clickLog(obj) {
    const id58 = ZZUTIL.cookie.get('id58');
    let urlstr = `https://lego.zhuanzhuan.com/page/mark?pagetype=ZHUANZHUANM&appid=ZHUANZHUAN&cookieid=${id58}&`;

    if (typeof obj === 'object' || !obj) { //传入参数为object null undefined时
      let actionflag = false; //判断是否需要自动补全actiontype的标识
      Object.keys(obj).forEach((key) => {
        urlstr += `${key}=${obj[key]}&`;
        if (key === 'actiontype') {
          actionflag = true;
        }
      });
      if (!actionflag) {
        console.log('请传入actiontype字段');
      }
    } else if (typeof obj === 'string') { //传入string时默认为actiontype属性
      urlstr += `actiontype=${obj}&`;
    }

    let img = new Image();
    img.src = `${urlstr}callback=?`;
  }

  callapp(event) {
    window.callApp.start({
      channelId: this.parsed.channelid,
      urlSearch: {
        openType: 'home',
      },
    });

    if (event) {
      const log = event.target.getAttribute('data-log');
      this.clickLog({ actiontype: log });
    }
  }

  render() {
    return (
      <div className="main">
        <div className="jd-1" >
            <span className="down" data-log="SEM40-BUTTONCLICK1" onClick={this.callapp} />
        </div>
        <div className="jd-2" >
            <span className="down" data-log="SEM40-BUTTONCLICK2" onClick={this.callapp} />
        </div>
        <div className="jd-3" >
            <span className="down" data-log="SEM40-BUTTONCLICK3" onClick={this.callapp} />
        </div>
        <div className="jd-4" />
      </div>
    );
  }
}

export default Jd;



