import $ from '@zz-yy/ajax-lite';
import ZZUTIL from '../../common/util';

const queryString = require('query-string');

class Phone extends React.Component {
  constructor() {
    super();
    this.parsed = queryString.parse(window.location.search);
    this.callapp = this.callapp.bind(this);
  }

  componentWillMount() {
    this.clickLog({ actiontype: 'SEM30-VIEW1' });
  }

  componentDidMount() {
    setTimeout(
      () => {
        if (this.parsed.isauto === 'true') {
          this.callapp();
        }
      }, 1700
    );
  }

  clickLog(obj) {
    const id58 = ZZUTIL.cookie.get('id58');
    let urlstr = `https://lego.58.com/page/mark?pagetype=ZHUANZHUANM&appid=ZHUANZHUAN&cookieid=${id58}&`;

    if (typeof obj === 'object' || !obj) { //传入参数为object null undefined时
      let actionflag = false; //判断是否需要自动补全actiontype的标识
      Object.keys(obj).forEach((key) => {
        urlstr += `${key}=${obj[key]}&`;
        if (key === 'actiontype') {
          actionflag = true;
        }
      });
      if (!actionflag) {
        console.log('请传入actiontype字段');
      }
    } else if (typeof obj === 'string') { //传入string时默认为actiontype属性
      urlstr += `actiontype=${obj}&`;
    }

    $.get({ url: `${urlstr}callback=?` });
  }

  callapp(event) {
    window.callApp.start({
      channelId: this.parsed.channelid,
      urlSearch: {
        openType: 'home',
      },
    });

    this.clickLog({ actiontype: 'SEM30-BUTTONCLICK1' });

    // if (event) {
    //   if (event.target.className.indexOf('down') !== -1) {
    //     this.clickLog({ actiontype: 'SEM30-BUTTONCLICK1' });
    //   } else {
    //     this.clickLog({ actiontype: 'SEM30-BANNERCLICK' });
    //   }
    // }
  }

  render() {
    return (
      <div className="main">
        <div className="phone-1">
          <span className="down" onClick={this.callapp} />
        </div>
        <div className="phone-2" />
        <div className="phone-3" />
        <div className="phone-4" />
        <div className="phone-5">
          <span className="down" onClick={this.callapp} />
        </div>
      </div>
    );
  }
}

export default Phone;



