/*eslint prefer-template: 0, prefer-rest-params: 0 */
/**
 * Created by wumingli on 2017/1/10.
 */
const cache = {};
const ZZUTIL = {
  doMake(originUrl, width, height) {
    let url = originUrl;
    if (url.indexOf('?') > -1) {
      url = url.substr(0, url.indexOf('?'));
    }
    if (width && height) {
      return `${url}?w=${width}&h=${height}`;
    } else if (width && !height) {
      return `${url}?w=${width}`;
    }
    return `${url}?h=${height}`;
  },
  setPicSize: (url, width = 200, height = 200) => {
    if (!url) {
      return '';
    }
    let handelUrl = url;
    let innerUrl = '';

    const re = /^(http:)/;
    if (re.test(url)) {
      handelUrl = handelUrl.replace(re, 'https:');
    }

    //微信头像
    if (handelUrl.indexOf('https://wx.qlogo.cn') > -1 || handelUrl.indexOf('http://wx.qlogo.cn') > -1) {
      const index = handelUrl.lastIndexOf('/');
      return `${handelUrl.substr(0, index)}/96`;
    } else if (handelUrl.indexOf('https://pic') > -1) {
      //pic的图片
      //已经有_100_100的尺寸设置
      const matchRule = /_\d+_\d+(\.png$|\.jpg$|\.gif$|\.jpeg$|\.bmp$|\.pic$)/i;
      if (matchRule.test(handelUrl)) {
        const bott = matchRule.exec(handelUrl)[0];
        const dot = bott.lastIndexOf('.');
        const suf = bott.substr(dot, bott.length - 1);
        const reg = new RegExp(bott);
        innerUrl = handelUrl.replace(reg, '') + suf;
      } else {
        innerUrl = handelUrl;
      }
      return ZZUTIL.doMake(innerUrl, width, height);
    } else if (!(handelUrl.indexOf('https') > -1)) {
      //没有协议的图片
      const key = /^\//g.test(handelUrl.trim()) ? handelUrl.substr(1) : handelUrl;
      let random;
      if (cache[key]) {
        random = cache[key];
      } else {
        random = cache[key] = Math.ceil(Math.random() * 8);
      }
      innerUrl = `https://pic${random}.58cdn.com.cn/zhuanzh/${key}`;
      return ZZUTIL.doMake(innerUrl, width, height);
    } else if (handelUrl.indexOf('https://zzpic') > -1) {
      //其他
      return ZZUTIL.doMake(handelUrl, width, height);
    }
    return handelUrl;
  },
  cookie: {
    get(name, encode) {
      const arg = `${name}=`;
      const alen = arg.length;
      const clen = document.cookie.length;
      let i = 0;
      let j = 0;
      while (i < clen) {
        j = i + alen;
        if (document.cookie.substring(i, j) === arg) {
          return this.getCookieVal(j, encode);
        }
        i = document.cookie.indexOf(' ', i) + 1;
        if (i === 0) {
          break;
        }
      }
      return null;
    },
    getname(cookieName, name) {
      const cookieVal = this.get(cookieName);
      const regex = new RegExp('[?&]' + encodeURIComponent(name) + '\\=([^&#]+)');
      const value = (cookieVal.match(regex) || ['', ''])[1];
      return decodeURIComponent(value);
    },
    set(name, value) {
      const argv = arguments;
      const argc = arguments.length;
      const now = new Date();
      const expires = argc > 2 ? argv[2] :
        new Date(now.getFullYear(), now.getMonth() + 1, now.getUTCDate());
      const path = argc > 3 ? argv[3] : '/';
      const domain = argc > 4 ? argv[4] : '.58.com';
      const secure = argc > 5 ? argv[5] : false;

      document.cookie = name + '=' +
        escape(value) +
        (expires === null ? '' : '; expires=' + expires.toGMTString()) +
        (path === null ? '' : '; path=' + path) +
        (domain === null ? '' : '; domain=' + domain) +
        (secure === true ? '; secure' : '');
    },
    remove(name) {
      if (this.get(name)) {
        this.set(name, '', new Date(1970, 1, 1));
      }
    },
    getCookieVal(offset, encode) {
      let endstr = document.cookie.indexOf(';', offset);
      if (endstr === -1) {
        endstr = document.cookie.length;
      }
      if (encode) {
        return unescape(document.cookie.substring(offset, endstr));
      }
      return document.cookie.substring(offset, endstr);
    },
  },
};

window.ZZUTIL = ZZUTIL;

export default ZZUTIL;



